// =======================================
//  Vertical Bar Chart
//=======================================


.sentio.bars-vertical {
  position: relative;
}

.sentio.bars-vertical .bar {
  background-color: #ddd;
  color: #000;
  font: 12px sans-serif;
  overflow: visible;
  position: absolute;
  white-space: nowrap;
}

.sentio.bars-vertical .bar:hover {
  background-color: #bbb;
}

.sentio.bars-vertical .bar-label {
  display: inline-block;
  margin: 5px;
  overflow: hidden;
  text-overflow: ellipsis;
}
